/* === preloader === */
.preloader {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #000;
  z-index: 1000;
  transition: all 1s ease; }
  .preloader__content {
    position: absolute;
    top: 50%;
    left: 0;
    right: 0;
    bottom: 0; }
    .preloader__content-item {
      width: 30%;
      height: 30%;
      margin: auto;
      background-image: url(".././image/sprite.svg#Vector");
      background-position: center;
      background-repeat: no-repeat;
      border-radius: 100%;
      animation: preloader__content-item 1.0s infinite ease-in-out; }

@keyframes preloader__content-item {
  0% {
    transform: scale(0); }
  100% {
    transform: scale(2);
    opacity: 0; } }

