.header {
  background-image: url("../image/header-bg.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover; }

.nav {
  position: relative;
  /*  */ }
  .nav__item--position {
    display: flex;
    justify-content: flex-start; }
    .nav__item--position li {
      width: 130px;
      height: 100px;
      display: flex;
      justify-content: center;
      align-items: center;
      border-bottom: 1px solid #E5E5E5; }
      .nav__item--position li a {
        position: relative;
        font-size: 12px;
        color: #FFFFFF; }
      .nav__item--position li a::before,
      .nav__item--position li a::after {
        position: absolute;
        content: "";
        bottom: -20%;
        left: -10%;
        transition: all ease 0.4s;
        width: 0;
        height: 0;
        background: #E8C300; }
      .nav__item--position li a:hover:before {
        width: 2px;
        height: 100%; }
      .nav__item--position li a:hover:after {
        width: 100%;
        height: 2px; }
    .nav__item--position li:nth-child(5) {
      margin-left: 30%; }
  .nav__logo {
    position: absolute;
    top: 25px;
    left: 50%;
    transform: translateX(-50%); }
    .nav__logo--svg {
      width: 100%;
      height: 100%; }
  .nav__mobile {
    display: none;
    position: relative;
    overflow: hidden;
    width: 40px;
    height: 40px;
    font-size: 0;
    text-indent: -9999px;
    appearance: none;
    box-shadow: none;
    border: none;
    cursor: pointer;
    z-index: 998; }
  .nav__mobile span {
    display: block;
    position: absolute;
    top: 20px;
    left: 0px;
    right: 0px;
    height: 2px;
    background: white;
    transition-duration: 0.3s, 0.3s;
    transition-delay: 0.3s, 0s; }
  .nav__mobile span::before,
  .nav__mobile span::after {
    position: absolute;
    display: block;
    width: 100%;
    height: 2px;
    left: 0;
    transition-duration: 0.3s, 0.3s;
    transition-delay: 0.3s, 0s;
    background: white;
    content: "";
    cursor: pointer; }
  .nav__mobile span::before {
    top: -10px; }
  .nav__mobile span::after {
    bottom: -10px; }
  .nav .nav__mobile--active span {
    transition-duration: 0.1s, 0.1s;
    transition-delay: 0.1s, 0s;
    background: none; }
  .nav .nav__mobile--active span::before,
  .nav .nav__mobile--active span::after {
    transition-duration: 0.3s, 0.3s;
    transition-delay: 0.3s, 0s; }
  .nav .nav__mobile--active span::before {
    top: 0;
    transform: rotate(45deg); }
  .nav .nav__mobile--active span::after {
    bottom: 0;
    transform: rotate(-45deg); }
  .nav .menu__mobile {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: black;
    transform: translateX(-100%);
    transition: all 0.8s ease;
    text-align: center; }
    .nav .menu__mobile ul {
      margin-top: 100px; }
      .nav .menu__mobile ul li {
        margin-top: 30px; }
        .nav .menu__mobile ul li a {
          font-size: 24px;
          color: #E8C300; }
  .nav .menu__mobile--active {
    transform: none;
    z-index: 9998; }

.header-content {
